import { Card } from "semantic-ui-react";

const Precio = props => {
  const {percent_change_1h, price} = props.datos;
  console.log(props);
  return (
    <Card color="green">
      <Card.Content>
        <Card.Header textAlign="left">Precio del bitcoin: ${price}</Card.Header>
      </Card.Content>
      <Card.Content extra>
        <Card.Description />
      </Card.Content>
    </Card>
  );
};

export default Precio;
